\n$blue: #4BAECE;\n$gray: #898686;\n$dark-gray: #565454;\n$coffee: #E5D7C0;\n$white: white;\n$caution: #f1807e;\n$medium: 700px;\n$large: 1200px;\n\n/*************************************/\n/************   Header   *************/\n/*************************************/\nheader {\n    display: flex;\n    justify-content: space-between;\n    flex-direction: column;\n    background-color: $blue;\n    padding: 1.5rem 5%;\n\n    h1 {\n        color: $white;\n        text-align: center;\n\n        span {\n            font-size: 1rem;\n        }\n    }       \n\n    button {\n        background-color: $white;\n        color: $blue;\n        padding: 0.5rem 2rem;\n        height: 2rem;\n        border: none;\n        border-radius: 30px;\n        margin: 0.5rem 0 0 auto;\n        width: 35%;\n    } \n\n    @media only screen and (min-width: $medium) {\n        flex-direction: row;\n        justify-content: space-between;\n        align-items: center;\n\n        button {\n            width: unset;\n            margin-top: 0.5rem;\n        }\n    }\n}\n\n\n/*************************************/\n/*************   Form   **************/\n/*************************************/\n\n.form{ \n\n    padding: 3rem;\n\n    form{\n\n        width: 80%;\n        max-width: 900px;\n        margin: 0 auto;\n\n        label {\n            margin: 0.5rem 0;\n        }\n\n        label span {\n            color: $caution;\n            font-size: small;\n            margin: 0 0.2rem;\n        }\n\n        .eg {\n            color: $dark-gray;\n        }\n\n        input, select {\n            display: block;\n            width: 80%;\n            padding: 0.5rem 1rem;\n            margin: 0.2rem auto 1rem auto;\n            background-color: $coffee;\n            border-radius: 30px;\n            border: 0.5px solid $gray;\n        }\n\n        .twentyFourHours {\n            display: flex;\n            flex-wrap: nowrap;\n            align-items: center;\n            margin: 2rem 0;\n\n            input {\n                width: 5%; \n                height: 1rem;\n                margin: auto auto auto 0.5rem;\n            }\n\n        }\n\n        .holiday {\n            display: flex;\n            flex-wrap: nowrap;\n            justify-content: space-between;\n            width: 90%;\n            align-items: center;\n        }\n\n        button {\n            display: block;\n            width: 30%;\n            padding: 0.5rem 1rem;\n            margin: 0.3rem auto;\n            background-color: $blue;\n            border-radius: 30px;\n            border: 0.5px solid $gray;\n            color: $white;\n        }\n\n\n        @media only screen and (min-width: $medium) {\n            display: grid;\n            grid-template-columns: repeat(2, 1fr);\n            grid-column-gap: 1rem;\n\n            input, select {\n                width: 90%;\n            }\n\n            .twentyFourHours {\n                grid-column: 1/3;\n                width: 15%;\n\n                input {\n                    width: unset;\n                    margin-left: 0.5rem;\n                }\n            }\n\n            .openHours {\n                grid-column: 1/3;\n                display: grid;\n                grid-template-columns: repeat(2, 1fr)\n            }\n\n            .holiday {\n                margin: 0.5rem 0;\n                grid-column: 1/3;\n            }\n\n            button {\n                display: block;\n                grid-column: 1/3;\n                justify-self: center;\n            }\n        }\n    }\n\n    .error {\n        text-align: center;\n        color: $caution;\n        margin-top: 1rem;\n    }\n}\n\n\n/*************************************/\n/***********   Finder   *************/\n/*************************************/\n\n.finder{\n\n    color: $gray;\n\n    .filter {\n        padding: 1.5rem;\n\n        h2 {\n            margin-bottom: 1rem;\n        }\n\n        h2:before {\n            font-family: \"Font Awesome 5 Free\"; \n            font-size: 1.2rem;\n            font-weight: 600;\n            content: \"\\f0b0\";\n            margin-right: 0.5rem;\n        }\n\n        form {\n            display: grid;\n            grid-template-columns: repeat(2, 1fr);\n\n            select {\n\n                display: block;\n                width: 90%;\n                padding: 0.5rem 1rem;\n                background-color: $white;\n                border-radius: 30px;\n                border: 0.5px solid $gray;\n            }\n\n            input {\n                color: $gray;\n            }\n        }\n\n        @media only screen and (min-width: $medium) {\n            display: grid;\n            grid-template-columns: 20% 80%;\n            align-items: center;\n\n            h2 {\n                grid-column: 1/2;\n                justify-self: start;\n                margin-bottom: 0;\n            }\n\n            form {\n                grid-column: 2/3;\n            }\n        }\n\n        @media only screen and (min-width: $large) {\n            form {\n                grid-template-columns: repeat(4, 1fr);\n            }\n        }\n    }\n    \n    ul {\n        padding: 1rem;\n        list-style: none;\n\n        li {\n            padding: 1rem;\n            margin-bottom: 1rem;\n            background-color: $coffee;\n            border-radius: 20px;\n\n            h2 {\n                margin-bottom: 0.5rem;\n            }\n\n            .detailsWrapper {\n\n                width: 100%;\n                margin-top: 1rem;\n                display: grid;\n                grid-template-columns: 30% 65%;\n                grid-column-gap: 5%;\n                place-items: center;\n\n                img {\n                    width: 100%;\n                    grid-column: 1/2;\n                    border-radius: 20px;\n                }\n\n                .details {\n                    width: 100%;\n                    grid-column: 2/3;\n                    margin-top: 1rem;\n                    display: grid;\n                    grid-template-columns: repeat(2, 1fr);\n                    grid-template-rows: repeat(3, 1fr);\n                    grid-row-gap: 0.5rem;\n            \n                    div{\n                        display: grid;\n                        grid-template-columns: 20% 80%;\n                        \n                        i {\n                            justify-self: center;\n                        }\n                    }\n\n                    div.featured {\n                        color: $blue;\n                    }\n\n\n                    .holiday{\n                        grid-column: 1/3;\n                        display: grid;\n                        grid-template-columns:repeat(auto-fit, minmax(10px, 1fr));\n                        padding: 0.2rem;\n                        grid-gap: 0 0.1rem;\n\n                        li {\n                            font-size: 14px;\n                            padding: 0.2rem 0;\n                            border-radius: 0;\n                            text-align: center;\n                        }\n\n                        li.open {\n                            color: $white;\n                            background-color: $blue;\n                        }\n\n                        li.close{\n                            color: $white;\n                            background-color: $gray;\n                        }\n                    }\n                }\n            }\n        }\n\n\n        @media only screen and (min-width: $medium) {\n            display: grid;\n            grid-template-columns: repeat(2, 1fr);\n            grid-gap: 1rem;\n        }\n\n        @media only screen and (min-width: $large) {\n            grid-template-columns: repeat(3, 1fr);\n        }\n    }\n}\n\n\n/*************************************/\n/***********   Message   *************/\n/*************************************/\n\n.message {\n\n    width: 80%;\n    height: 30%;\n    position:fixed;\n    inset: 0;\n    margin: auto;\n    background-color: $gray;\n    border-radius: 20px;\n    padding: 2rem;\n    text-align: center;\n\n    p {\n        grid-column: 1 / 3;\n        color: $caution;\n        font-weight: bold;\n        margin-bottom: 2rem;\n    }\n\n    button {\n        width: 70%;\n        border: 0;\n        border-radius: 30px;\n        background-color: $coffee;\n        padding: 0.5rem 1rem;\n        margin-bottom: 1rem;\n    }\n\n    @media only screen and (min-width: $large) {\n        width: 40%;\n        display: grid;\n        grid-template-columns: repeat(2, 1fr);\n        grid-template-rows: repeat(2, 1fr);\n        place-items: center;\n\n    }\n}\n\n\n/*************************************/\n/***********   Footer   *************/\n/*************************************/\n\nfooter {\n    p {\n        color: white;\n        background-color: #4BAECE;\n        padding: 1.5rem;\n        text-align: center;\n    }       \n}"],"sourceRoot":""}]);
